::-webkit-scrollbar{
  width: 10px;
  background: lightpink;
}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(180deg, #d0368a 0%, #708ad4 99%);
  box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
  border-radius: 3px;
}

html {
  scroll-behavior: smooth;
}

.input{
  background: none;
  border: none;
  border-bottom: 1px solid #708ad4;
  outline: none;
  color: white;
  height: 2rem;
  font-size: .9em;
  padding: 0;
}

.red{
  color: red;
}

.link{
  opacity: 0;
}

.card:hover .link{
  opacity: 1;
}

.news{
  transition: 1s;
  height: 10rem;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  width: 0%;
  opacity: .8;
  position: absolute;
}

.img{
  width: 35rem;
}

.card:hover .news{
  width: 50%;
}

.btn{
  height: 2rem;
  width: 7rem;
  translate: 0 15px;
  background: none;
  color: white;
  border-radius: 10px;
  border: 1px solid #9ad1c9;
  transition: 1s;
  font-weight: 500;
  text-transform: uppercase;
}

.btn:hover{
  background: #9ad1c9;
  color: black;
}

