.typing{
  font-size: 40px;
  animation: typing 4s steps(15) infinite alternate, blink .3s step-end infinite alternate;
  white-space: nowrap;
  border-right: 1px solid white;
  overflow: hidden;
  text-transform: capitalize;
  width: 0;
  text-align: left;
}

@keyframes typing{
  70%, 100%{
    width: 11ch;
  }
}

@keyframes blink{
  50%{
    border: transparent;
  }
}

@keyframes up {
  from{
    translate: 0 150px;
  }
  to{
    translate: 0 0;
  }
}

@keyframes opacity{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}